.md-popover.md-tooltip{z-index:111;pointer-events:none}.md-popover.md-tooltip>.content{font-size:10px;padding:0 8px;height:22px;line-height:22px;text-transform:none;white-space:nowrap;color:var(--md-surface);background-color:var(--md-on-surface-medium)}@media (max-width:960px){.md-popover.md-tooltip>.content{height:32px;font-size:14px;line-height:32px}}.md-popover.md-tooltip .md-popover-enter-active{transition-duration:.15s}