.md-snackbar{background-color:var(--md-elevation-6);box-shadow:0 3px 5px -1px rgba(0,0,0,.2),0 6px 10px 0 rgba(0,0,0,.14),0 1px 18px 0 rgba(0,0,0,.12);min-width:288px;max-width:568px;min-height:48px;max-height:80px;padding:14px 24px;display:flex;align-items:center;position:fixed;z-index:1000;border-radius:2px;background-color:var(--md-on-background);color:var(--md-background)}.md-snackbar.md-position-center{margin:0 auto;right:0;bottom:0;left:0}.md-snackbar.md-position-left{bottom:24px;left:24px}@media (max-width:600px){.md-snackbar{left:0;transform:none;border-radius:0}}.md-snackbar-enter,.md-snackbar-leave{transition:.4s cubic-bezier(.4, 0, .2, 1);transition-property:opacity,transform;will-change:background-color,color,opacity,transform}.md-snackbar-enter,.md-snackbar-leave.md-snackbar-leave-active{opacity:0}.md-snackbar-enter.md-position-center,.md-snackbar-leave.md-snackbar-leave-active.md-position-center{transform:translate3d(0,calc(100% + 8px),0)}.md-snackbar-enter.md-position-left,.md-snackbar-leave.md-snackbar-leave-active.md-position-left{transform:translate3d(0,calc(100% + 32px),0)}.md-snackbar-enter.md-snackbar-enter-active,.md-snackbar-leave{opacity:1}.md-snackbar-enter.md-snackbar-enter-active.md-position-center,.md-snackbar-enter.md-snackbar-enter-active.md-position-left,.md-snackbar-leave.md-position-center,.md-snackbar-leave.md-position-left{transform:none}.md-snackbar-content{flex:1;display:flex;align-items:center;justify-content:space-between;transition:opacity .38s cubic-bezier(.55, 0, .55, .2)}.md-snackbar-content .md-button{min-width:0;margin:-8px -8px -8px 36px}@media (max-width:600px){.md-snackbar-content .md-button{margin-left:12px}}.md-snackbar-content .md-button+.md-button{margin-left:16px}